import { Component, Injector, OnInit } from '@angular/core';
import { ActiveModalRef, DismissReasons } from '@bmc-ux/adapt-angular';
import { RxModalClass } from '@helix/platform/ui-kit';
import { IMissingArchiveAssociation } from './archive-associations-control.types';

@Component({
  selector: 'rx-missing-archive-definitions-modal-selector',
  templateUrl: './missing-archive-definitions-modal.component.html'
})
export class MissingArchiveDefinitionsModalComponent extends RxModalClass implements OnInit {
  missingAssociations: IMissingArchiveAssociation[] = this.activeModalRef.getData().missingAssociations;

  constructor(private activeModalRef: ActiveModalRef, protected injector: Injector) {
    super(activeModalRef, injector);
  }

  ngOnInit(): void {
    super.ngOnInit();
  }

  close(): void {
    this.activeModalRef.dismiss(DismissReasons.CLOSE_BTN);
  }
}
